


























































import {Vue, Component} from 'vue-property-decorator';
import {Panel, Button, Divider, Toast, Collapse, CollapseItem, Overlay} from 'vant';
import {notes, NoteType} from '@/util/note';
import {Originals, OriginalApply} from '@/util/diamond_util';
import {
    AddressBaseModel,
    AuthOriginalTxModel, AuthAdminType,
    default as OriginalAddressModel,
} from '../../model/original_address_model';
import {CashboxJs} from '@/util/cashboxjs';
import {DiamondUtil, Status} from '../../util/diamond_util';
import 'vant/lib/panel/style';
import 'vant/lib/button/style';
import 'vant/lib/divider/style';
import 'vant/lib/collapse/style';
import 'vant/lib/collapse-item/style';
import 'vant/lib/toast/style';
import 'vant/lib/overlay/style';
import {ToastUtil} from "../../util/toast";
import {ExtrinsicStatus} from "@polkadot/types/interfaces";

Vue.use(Panel).use(Button).use(Divider).use(Collapse).use(CollapseItem).use(Toast).use(Overlay);
@Component
export default class OriginalAddr extends Vue {

    private authTypeText = "";              //授权方式
    private activeNames = ['0'];            //折叠卡片，默认配置数据
    private txName = '';                    //授权姓名
    private txAddress = '';                 //授权地址
    private allAdminCount = 0;
    private allowAdminCount = 0;
    private originAddressList = Array();
    private authOriginalTxModel = new AuthOriginalTxModel();
    private isShowLoading = false;

    public created(): void {
        this.$store.state.title = '授权状态列表';
        {
            (window as any).nativeSignMsgToJsResult = this.nativeSignMsgToJsResult;
        }
        this.authOriginalTxModel = this.$store.state.authOriginalTxModel;
        if (this.authOriginalTxModel.authType === AuthAdminType.AddAdmin) {
            this.authTypeText = this.$t('authOriginal').toString();
        } else {
            this.authTypeText = this.$t('cancelOriginal').toString();
        }
        this.txAddress = this.authOriginalTxModel.txAddress;
        this.txName = this.authOriginalTxModel.txName;
    }

    public mounted() {
        //每次回到这个页面，都执行下计数，看看几个人完成签名了
        this.originAddressList = this.authOriginalTxModel.originalAddressModelList;
        console.log("originAddressList.length=>" + this.authOriginalTxModel.originalAddressModelList.length);
        this.allAdminCount = this.authOriginalTxModel.originalAddressModelList.length;
        console.log('this.allAdminCount===>' + this.allAdminCount);
        let tempCount = 0;
        this.authOriginalTxModel.originalAddressModelList.forEach((model: AddressBaseModel) => {
            if (model.signStatus === true) {
                tempCount = tempCount + 1;
            }
        });
        this.allowAdminCount = tempCount;
    }

    // 发起签名请求
    public applyForSign(index: number): void {
        const addressList = this.authOriginalTxModel.originalAddressModelList;
        const model = addressList[index] as OriginalAddressModel;
        if (model.signStatus) {
            ToastUtil.show('该管理员已经完成签名了！~~~');
            return;
        }
        console.log("applyForSign model.address" + model.address + index);
        this.authOriginalTxModel.nowAdminAddress = model.address;
        this.authOriginalTxModel.nowAdminName = model.name;
        this.$store.commit('updateTxAdminAddressAndName', this.authOriginalTxModel); //记录向申请操作的管理员信息
        this.$router.push({name: 'original_sign'});
    }

    // 验签名个数，调用自签
    public async upTxToChain() {
        let signList = <any>[];
        this.authOriginalTxModel.originalAddressModelList.forEach((adminModel: OriginalAddressModel) => {
            if (adminModel.signStatus === true) {
                signList.push({signInfo: adminModel["signInfo"], accountId: adminModel["address"]});
            }
        });
        if (this.allowAdminCount >= 2 && signList.length >= 2) {
            console.log("this.$store.state.nativeChainAddress:" + this.$store.state.nativeChainAddress);
            let assembleTxData = await DiamondUtil.getInstance().callAdminOperation(this.authOriginalTxModel.authType,
                this.$store.state.nativeChainAddress, this.txAddress, signList, this.txName);
            if (assembleTxData === "") {
                ToastUtil.fail("拼装调用链数据失败！");
                return;
            }
            CashboxJs.getInstance().callNativeSignMsgToJs(assembleTxData);
        } else {
            ToastUtil.show('授权管理员人数不够，现只有' + this.allowAdminCount + "人同意签名。");
        }
    }

    //自签完成，准备上链
    public nativeSignMsgToJsResult(val: string): void {
        ToastUtil.show('nativeSignMsgToJsResult===>' + val);
        console.log("nativeSignMsgToJsResult===>" + val);
        DiamondUtil.getInstance().sendToChain(val, (status: ExtrinsicStatus) => {
            this.isShowLoading = false;
            if (status.isFinalized) {//完成
                console.log("use balance is finished!===>");
                ToastUtil.show("信息上链已完成，可在浏览器查询交易状态");
            }
            if (status.isBroadcast) {//广播
                console.log("status.isBroadcast===>");
                console.log("use balance is finished!===>");
                ToastUtil.show("信息上链已完成，可在浏览器查询交易状态");
            }
            if (status.isDropped) {//丢弃
                console.log("status.isDropped===>");
                ToastUtil.fail("信息上链失败，操作已被丢弃");
                return;
            }
            if (status.isInvalid) {//无效
                console.log("status.isInvalid!===>");
                ToastUtil.fail("信息上链失败，无效操作");
                return;
            }
            this.$router.push({name: 'original_process'});
        }).catch((reason) => {
            console.log("reason:" + reason);
            ToastUtil.fail("信息上链失败，错误原因" + reason);
        });
    }
}
